























































  import { SideBarItem } from '@/types';
import Vue from 'vue';
  import Component from "vue-class-component";
  import { Prop, Watch } from "vue-property-decorator";

  @Component
  export default class SideBar extends Vue {
    isShowing = false;
    items: SideBarItem[] = [
      {
        title: 'Dashboard',
        icon: 'mdi-view-dashboard',
        path: '/',
      },
      {
        title: 'Configuration',
        icon: 'mdi-cog',
        path: '/config',
      },
      {
        title: 'Message Creator',
        icon: 'mdi-email',
        path: '/message-creator',
      },
      {
        title: 'About',
        icon: 'mdi-information',
        path: '/about',
      },
      {
        title: 'Help',
        icon: 'mdi-help-circle',
        path: '/help',
      },
    ];

    selectedItem = 0;
    
    @Prop(Boolean) value!: boolean;
    @Prop(Boolean) disabled!: boolean;

    mounted() {
      this.isShowing = this.value;
    }

    @Watch('value')
    valueChanged(val: boolean) {
      this.isShowing = val;
    }

    @Watch('isShowing')
    isShowingChanged(val: boolean) {
      this.$emit('input', val); 
    }

    @Watch('$route.path', { immediate: true, deep: true })
    onPathChange(value: string) {
      let option;
      let index;
      for ([index, option] of Object.entries(this.items)) {
        if (option.path === value) {
          this.selectedItem = parseInt(index);
          break;
        }
      }
    }
  }
